.blog-list {
    width: calc(100vw - 20px);
    padding: 10px;
}

.blog-list-item {
    width: 100%;
    margin-bottom: 15px;
    height: 160px;
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
    cursor: pointer;
}

.blog-list-item-img {
    width: 100%;
    height: 100px;
    line-height: 100px;
    float: left;
    background-size: cover !important;
    border-radius: var(--border-radius) var(--border-radius) 0 0;
}

.blog-list-item-text {
    width: calc(100% - 20px);
    height: 40px;
    padding: 10px;
    background: var(--main-bg-color-0);
    float: left;
    border-radius: 0 0 var(--border-radius) var(--border-radius);
}

.blog-list-item-title {
    width: 100%;
    line-height: 20px;
    float: left;
    font-size: 20px;
    font-weight: bold;
    display: flex;
}

.blog-list-item-title .left {
    width: 50%;
}

.blog-list-item-title .right {
    width: 50%;
    text-align: right;
    font-size: 10px;
    color: var(--primary-color);
}


.blog-list-item-subtitle {
    width: 100%;
    height: 20px;
    line-height: 20px;
    float: left;
}

.blog-user-card {
    width: 100vw;
    height: 300px;
    background-size: cover !important;
    box-shadow: var(--box-shadow-active);
    color: white;
}

.blog-user-avatar {
    width: 100vw;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
}


.blog-user-avatar-img {
    width: 150px;
    height: 150px;
    border-radius: 150px;
    box-shadow: var(--box-shadow);
}

.blog-user-info {
    width: 100vw;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
}

.blog-detail {
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 50px;
}

.blog-detail-title {
    text-align: center;
    height: 30px;
    line-height: 30px;
    font-size: 30px;
    font-weight: bold;
}

ol li {
    word-break: break-all;
}