/*layout*/
.header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 50px;
    line-height: 50px;
    width: 100vw;
    display: -webkit-flex; /* Safari */
    display: flex;
    z-index: 100;
}

.header.active {
    /*backdrop-filter: var(--main-filter);*/
    /*background: var(--main-mask-bg);*/
    /*background: var(--main-bg-color-0);*/
    /*background: var(--primary-color);*/
    /*color: white;*/
    background: var(--main-bg-color-0);
}

.header.midActive {
    box-shadow: var(--box-shadow);
}

.header-nav-left {
    width: calc(33.3% - 5px);
    text-align: left;
    padding-left: 5px;
    cursor: pointer;
    font-size: 30px;
    color: var(--primary-color);
    font-weight: bold;
}

.header-nav-mid {
    width: 33.3%;
    text-align: center;
    font-weight: bold;
    font-size: large;
    font-weight: bold;
}

.header-nav-right {
    width: calc(33.3% - 5px);
    text-align: right;
    padding-right: 5px;
    font-size: 30px;
    cursor: pointer;
    color: var(--primary-color);
    font-weight: bold;
}

.main {
    position: absolute;
    top: 0vh;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.footer {
    position: fixed;
    bottom: 0px;
    right: 0;
    left: 0;
    width: 100vw;
    height: 10vh;
    line-height: 10vh;
    border-top: 1px solid var(--main-bg-color-1);
}

/*button*/
.sys-btn {
    height: 50px;
    width: calc(100% - 20px);
    line-height: 50px;
    margin: 10px;
    border-radius: 4px;
    background: var(--main-bg-color-0);
    color: var(--primary-color);
    font-weight: bold;
    text-align: center;
    cursor: pointer;
    box-shadow: var(--box-shadow);
}

.sys-icon {
    box-shadow: var(--box-shadow);
    border-radius: 25px;
    color: var(--primary-color);
    font-size: 25px;
    padding: 5px;
}